@import url(https://fonts.googleapis.com/css?family=Material+Icons+Outlined);
@import url(https://fonts.googleapis.com/css?family=Material+Icons+Round);
@import url(https://fonts.googleapis.com/css?family=Material+Icons);
:root {
    --font-color: #4F4F4F;
    --placeholder-color: rgb(168, 168, 168);
    --score-color: rgb(230, 255, 92);
    --primary-color: #1E86FF;
}
.search-input{
    border: 1px solid var(--placeholder-color);
    border-radius: 1em;
    max-width: 600px;
    height: 40px;
    display: flex;
    align-items: center;
    margin: 20px auto;
    padding: 0 10px;
}

.search-input input{
    border: none;
    outline: none;
    color: var(--font-color);
    font-size: 1em;
    width: 100%;
}

.search-input input::-webkit-input-placeholder{
    color: var(--placeholder-color);
}

.search-input input:-ms-input-placeholder{
    color: var(--placeholder-color);
}

.search-input input::placeholder{
    color: var(--placeholder-color);
}

.search-input__icon{
    color: var(--placeholder-color);
    margin: 0 10px 0 0;
}

.search-input__icon--text{
    color: var(--font-color);
}
.github-element{
    margin: 15px auto;
    width: 800px;

    

    border-radius: 10px;
    padding: 5px;
    text-align: left;
    display: flex;
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.1);
}

.github-element img{
    width: 120px;
    height: 120px;
}

.github-element__info{
    width: 100%;
    max-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.github-element__info p {
    margin-top: 0;
}

.github-element__score-icon{
    margin: 0 5px;
    position: relative;
    font-size: 1.4em;
    color: var(--score-color);
    -webkit-text-stroke: 1px #897EF5;
    top: 4px;
}

.pages{
    display: flex;
    justify-content: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: var(--placeholder-color);
}

.pages__arrow, .pages__number, .pages__more{
        width: 32px;
        height: 32px;
        border: 1px solid;
        border-radius: 4px;
        margin: 5px;
}

.pages__arrow:hover, .pages__number:hover{
    color: var(--primary-color);
    border-color: var(--primary-color);
}

.pages p{
    margin: auto 0;    
}

.pages__arrow--disabled{
    pointer-events: none;
    width: 32px;
    height: 32px;
    border: 1px solid;
    border-radius: 4px;
    margin: 5px;
    -webkit-filter: brightness(1.25);
            filter: brightness(1.25);
    display: flex;
    justify-content: center;
}

.pages__number, .pages__arrow{
    color: var(--placeholder-color);
    border-color: var(--placeholder-color);
    font-family: 'Roboto', sans-serif;
    cursor: pointer;
    display: flex;
    justify-content: center;
}

.pages__number--current, .pages__arrow--current{
    border-color: var(--primary-color);
    background: var(--primary-color);
    color: white;
    pointer-events: none;
}

.pages__more{
    position: relative;
    top: -11px;
    pointer-events: none;
    font-weight: 700;
    font-size: 2rem;
    border-color: rgba(0, 0, 0, 0);
    color: var(--placeholder-color);
}
.App {
  text-align: center;
}
